.detalleContenedor {
    display: grid;
    grid-template-columns: 2fr 1fr;
    background-color: #8da3a6;
    padding: .5rem;
    gap: 1rem;
    width: 50%;
    margin: auto;
    margin-bottom: 5rem;
    box-shadow: #ab9077 0 0 30px 3px;
    border-radius: .3rem;
}
.detalleImg {
    width: 100%;
    height: 100%;
    object-fit: contain;
    border-bottom-left-radius: .3rem;
    border-top-left-radius: .3rem;
}
.detallesProducto {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 1rem;
}
.detallesProducto p {
    margin: 0;
}
.detallesProducto h4 {
    font-size: 1.5rem;
}
@media (max-width: 1100px) {
    .detalleContenedor {
        width: 70%;
    }
}
@media (max-width: 770px) {
    .detalleContenedor {
        width: 85%;
    }
}
@media (max-width: 640px) {
    .detalleContenedor {
        width: 100%;
    }
}
@media (max-width: 540px) {
    .detalleContenedor {
        display: flex;
        flex-direction: column;
    }
    .detalleImg {
        border-radius: 0;
    }
}